.page-footer {
  .font();
  font-size: 14px;
  font-weight: 400;
  color: @primary-color;
  text-align: center;
  background-color: @secondary-color;
  width: 100%;
  height: 284px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.page-footer__logo-image {
  &:hover {
    g path {
      fill: @pink;
    }
  }

  &:active {
    path {
      fill: fade(@primary-color, 30%);
    }

    g path {
      fill: fade(@primary-color, 30%);
    }
  }
}

@media (max-width: @mobile-width) {
  .page-footer--index {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .page-footer {
    width: 620px;
    height: 127px;
    margin: 0 auto;
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: @desktop-width) {
  .page-footer {
    width: 940px;
    height: 197px;
    flex-direction: row;
    font-size: 18px;
  }

  .page-footer__logo-image {
    margin-right: 170px;
  }
}
